


























































import App from '../../../../App.vue'
import Api from '../../../../api.js'
export default {
	data() {
		return {
			type: '',
			account: '',
			couponId: '',
			coupon: {}
		}
	},
	onLoad(options) {
		var that = this
		that.type = options.type
		var l = options.result.split(":")
		that.account = l[0]
		that.couponId = l[1]
		uni.request({
			url: Api.companyConsume() + "/" + that.couponId,
			header: {
				token: App.getToken()
			},
			data: {
				companyId: 5
			},
			success: function(res) {
				console.log(res)
				if (res.data.code == 200) {
					that.coupon = res.data.value
					var s = new Date(that.coupon.startTime)
					var e = new Date(that.coupon.endTime)
					that.coupon.startTime = s.toLocaleDateString()
					that.coupon.endTime = e.toLocaleDateString()
					that.coupon.availableTime = (e.getTime() - s.getTime()) / 86400000
				} else {
					uni.showToast({
						title: '获取券详情失败',
						icon: 'none'
					})
				}
			}
		})
	},
	methods: {
		sure: function() {
			var that = this
			console.log(that.couponId)
			console.log(that.account)
			uni.request({
				url: Api.companyConsume(),
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					token: App.getToken()
				},
				data: {
					couponId: that.couponId,
					account: that.account,
					companyId: 5
				},
				success: function(res) {
					console.log(res)
					if (res.data.code == 200) {
						uni.showToast({
							title: '兑换成功'
						})
						uni.navigateTo({
							url: './sure',
						})
					} else if (res.data.code == 1009) {
						uni.showToast({
							title: '您没有这个权限',
							icon: 'none'
						})
					} else {
						uni.showToast({
							title: '兑换失败',
							icon: 'none'
						})
					}
				}
			})
		}
	}

}
